<h1 id="android-content-providers" tabindex="-1"><a class="header-anchor" href="#android-content-providers" aria-hidden="true">#</a> Android Content Providers</h1>
<p>Content Providers are one of the four different fundamental application components all Android applications consists of. We use them to give other applications access to the data in our application, oe we can use content providers in other applications to get access to their data.</p>
